* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

body {
  width: 1024px;
  height: 984px;
  font-family: Roboto, sans-serif;
  font-weight: 500;
}

@media (width >= 1200px) {
  body {
    width: 1200px;
  }
}

.header {
  text-transform: uppercase;
